import { Injectable } from '@angular/core';
import { HTTP } from '@ionic-native/http';

import 'rxjs/add/operator/map';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/catch';

@Injectable()
export default class CustomHttp extends HTTP {
  private _defaultHeaders = {
    'Accept': 'application/json',
    'Content-Type': 'application/json'
  };

  constructor() {
    super();
    for(const k in this._defaultHeaders) {
      if(typeof this._defaultHeaders[k] === 'string') {
        this.setHeader('', k, this._defaultHeaders[k]);
      }
    }
  }

  get(url, parameters = {}, headers = {}) {
    return this.get(url, parameters, headers)
  }

  post(url, body = {}, headers = {}) {
    return super.post(url, body, headers)
  }

  put(url, body = {}, headers = {}) {
    return this.put(url, body, headers)
  }

  patch(url, body = {}, headers = {}) {
    return this.patch(url, body, headers)
  }

  delete(url, parameters = {}, headers = {}) {
    return this.get(url, parameters, headers)
  }
}



